@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&family=Mulish:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: 'Muli', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #172232;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.btn {
  outline: none;
  border: none;
  cursor: pointer;
}

.btn:hover {
  opacity: .9;
}

.btn-link {
  font-size: 13px;
  text-transform: uppercase;
  color: #3e68ff;
  margin: 40px auto;
  background: transparent;
  text-decoration: underline;
}

.App {
  text-align: center;
}

.Card{border-radius:8px;box-shadow:0 4px 14px -5px rgba(0,0,0,0.2);border:1px solid #f1f1f1}

.SkeletonLoader-Bone{background:#d2d2d2;vertical-align:middle}.SkeletonLoader-Bone--round{border-radius:36px}

.SkeletonLoader{position:relative}.SkeletonLoader .SkeletonLoader-content{position:static;position:initial;overflow:auto;z-index:1}.SkeletonLoader .SkeletonLoader-shimmer{position:absolute;left:0;top:0;bottom:0;right:0;background-image:linear-gradient(to right, rgba(255,255,255,0.65) 0%, rgba(255,255,255,0.45) 10%, rgba(255,255,255,0.65) 20%);background-size:200% 100px;background-position:100% 0;pointer-events:none;z-index:2}.SkeletonLoader--darkBG .SkeletonLoader-shimmer{background-image:linear-gradient(to right, rgba(23,34,50,0.85) 0%, rgba(23,34,50,0.75) 10%, rgba(23,34,50,0.85) 20%)}.SkeletonLoader--animate .SkeletonLoader-shimmer{-webkit-animation:animation-Shimmer 1.2s forwards infinite linear;animation:animation-Shimmer 1.2s forwards infinite linear}@-webkit-keyframes animation-Shimmer{0%{background-position:100% 0}100%{background-position:-100% 0}}@keyframes animation-Shimmer{0%{background-position:100% 0}100%{background-position:-100% 0}}

.UserExpense{position:relative;max-width:420px;min-width:360px;margin:0 auto;text-align:left;background:#fff;overflow:hidden}.UserExpense .UserExpense-details{padding:0 16px}.UserExpense .title{margin:16px 0 4px;font-size:12px;font-weight:bold;line-height:16px;text-transform:uppercase;color:#afafaf}.UserExpense .description{line-height:20px;font-size:14px;margin-bottom:4px}.UserExpense .description+.title{margin-top:28px}.UserExpense .expense-amount{font-size:24px;font-weight:bold;color:#10d6a0}.UserExpense .UserExpense-actions{margin-top:28px;padding:16px 0;text-align:right;border-top:1px solid #e8e8e8}.UserExpense .UserExpense-actions-btn{display:inline-block;vertical-align:middle;padding:0 12px;height:48px;min-width:100px;line-height:48px;font-size:13px;border-radius:24px;text-align:center;font-weight:bold;text-transform:uppercase}.UserExpense .UserExpense-actions-btn:nth-child(n+2){margin-left:16px}.UserExpense .UserExpense-actions-btn--approve{background:#16af16;color:#fff}.UserExpense .UserExpense-actions-btn--request{background:#2121bb;color:#fff}.VirtualCard{position:relative;height:248px;border-radius:12px;background-color:#172232;padding:16px;color:#f2f2f2}.VirtualCard .VirtualCard-profileImg{display:inline-block;width:48px;height:48px;padding:4px;vertical-align:top;border-radius:50%}.VirtualCard .VirtualCard-details{display:inline-block;vertical-align:top;margin-left:16px;text-transform:uppercase;font-weight:bold}.VirtualCard .VirtualCard-title{font-size:20px;margin-bottom:8px}.VirtualCard .VirtualCard-description{font-size:14px;opacity:.8}.UserExpenseLoader{cursor:wait}

.Demo{position:relative;margin:40px 0}

